import React, { Component } from "react";
import { useState, useEffect,useCallback } from 'react';
import {BrowserRouter as Router,Switch,Route,Link,Redirect,} from "react-router-dom";
import $ from 'jquery';
import styles from './css/blog.module.css';

import csrftoken from  "./csrf"
import handleSubmit, {handleUpdate} from './postForm'

import parse from 'html-react-parser';

const color =['orange','red','blue','green','lilac','yellow','violet']

export default function Example(){
  const [blogs, setBlogs] = useState([]);
  let backend_url =process.env.NODE_ENV=='production' ? process.env.REACT_APP_BACKEND_URL:'http://127.0.0.1:8000';

  const fetchTasks = useCallback(() => {
    console.log('Fetching...')

    fetch(backend_url+'/api/blog/')
    .then(response => response.json())
    .then(data =>setBlogs(data))
  },[backend_url])  
  useEffect(() => {
    fetchTasks();
  },[fetchTasks]);
  
  function updatestory(event,p_id){
    const url=backend_url+'/api/blog/'+p_id;
    const data = {
      'update_count':1
    }
    handleUpdate (data,url)

  }
  return (
       <div className={styles["contentb" ]}>
          
          {blogs&&blogs.map((story,index) =>
            <div onClick={(e) => updatestory(e,story.id)} className={styles["box"]} style={{borderTopColor: color[index],height:'23em'}}>
            <Link to={"/"+story.id}>
            <span className={styles["story-topic"]}>{story.topic}</span>
            <h5 className={styles["story-subject"]}>{story.subject.toUpperCase()}</h5>
            <p className={styles["story-content"]}>{parse(story.content)}</p>
            <img alt='' className={styles["story-avatar"]} src={require('./media/'+'ml.jpg').default}  />
            <p> <i className={styles["views-eye"] +' '+"fa fa-eye"} aria-hidden="true"> {story.views}</i>
            </p>
            </Link> 
            </div>
            )
        }
        </div>
  );
}