import React, { Component } from "react";
import { useState, useEffect,useCallback } from 'react';
import {BrowserRouter as Router,Switch,Route,Link,Redirect,useParams} from "react-router-dom";
import $ from 'jquery';

// import styles from './css/App.module.css';
import styles from './css/detail.module.css';

// import parse from 'html-react-parser';

import parse from 'html-react-parser/dist/html-react-parser'
// import Parser from 'html-react-parser/dist/html-react-parser'
// import parse from 'html-react-parser';

export default function Example() {
  const [blog, setBlog] = useState();
  const [story, setStory] = useState("");
  const [pk, setPk] = useState(useParams().id);
  let backend_url =process.env.NODE_ENV=='production' ? process.env.REACT_APP_BACKEND_URL:'http://127.0.0.1:8000';

  const fetchTasks = useCallback(() => {
    fetch(backend_url+'/api/blog/'+pk)
    .then(response => response.json())
    .then(data =>setStory(data))
  },[backend_url,pk])
  useEffect(() => {
    fetchTasks();
  },[fetchTasks]);
    return (
        <div className={styles["big-box"]} style={{borderTopColor: 'red'}}>
        {story&&story.map((story,index) =>
            <div className={styles["box"]} style={{borderTopColor: 'red'}}>
              <span className={styles["story-topic"]}>{story.topic}</span>
              <h1 className={styles["story-subject"]}>{story.subject}</h1>
              <p className={styles["story-content"]}>{parse((story.content))}</p>
              <img alt='' className={styles["story-avatar"]} src={require('./media/'+'ml.jpg').default}  />
            </div>
        )}
        </div>
  );
}
